import { createServerClient } from "@/lib/supabase/server-client"
import { NextResponse } from "next/server"

export async function POST(request: Request) {
  try {
    const supabase = await createServerClient()
    
    const {
      data: { user },
    } = await supabase.auth.getUser()

    if (!user) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 })
    }

    const body = await request.json()
    const { title, description, genre, bpm, price, tags, cover_image_url, demo_audio_url, file_url, has_discount, discount_percent } = body

    if (!title || !description || !genre || price === undefined || !file_url) {
      const missingFields = []
      if (!title) missingFields.push("title")
      if (!description) missingFields.push("description")
      if (!genre) missingFields.push("genre")
      if (price === undefined) missingFields.push("price")
      if (!file_url) missingFields.push("file_url")

      return NextResponse.json(
        { 
          error: "Validation failed",
          details: `Missing required fields: ${missingFields.join(", ")}`,
          missingFields
        },
        { status: 400 }
      )
    }

    if (!file_url.startsWith("https://")) {
      return NextResponse.json(
        { 
          error: "Invalid file URL",
          details: "file_url must be a full HTTPS URL from Supabase Storage"
        },
        { status: 400 }
      )
    }

    const { data: profile, error: profileError } = await supabase
      .from("profiles")
      .select("plan")
      .eq("id", user.id)
      .single()

    if (profileError || !profile) {
      return NextResponse.json(
        { 
          error: "User profile not found",
          details: "Could not retrieve user profile from database"
        },
        { status: 400 }
      )
    }

    const { data: packsThisMonth, error: statsError } = await supabase.rpc("get_packs_this_month", {
      p_user_id: user.id,
    })

    if (statsError) {
      console.error("[v0] Error checking upload limit:", statsError)
      return NextResponse.json(
        { 
          error: "Could not verify upload limit",
          details: "Failed to check your monthly upload quota"
        },
        { status: 500 }
      )
    }

    // Check if user can upload more packs
    const planLimits: Record<string, number | null> = {
      "free": 3,
      "de_0_a_hit": 10,
      "de-0-a-hit": 10,
      "studio_plus": null, // unlimited
      "studio-plus": null, // unlimited
    }

    const monthlyLimit = planLimits[profile.plan]
    if (monthlyLimit !== null && packsThisMonth >= monthlyLimit) {
      return NextResponse.json(
        { 
          error: "Upload limit reached",
          details: `You can upload ${monthlyLimit} packs per month on the ${profile.plan} plan. Try again next month or upgrade.`,
          current: packsThisMonth,
          limit: monthlyLimit
        },
        { status: 403 }
      )
    }

    const { data: pack, error: packError } = await supabase
      .from("packs")
      .insert({
        user_id: user.id,
        title,
        description,
        genre,
        bpm: bpm || null,
        price: Number.parseInt(price),
        cover_image_url: cover_image_url || null,
        demo_audio_url,
        file_url,
        tags: tags || [],
        has_discount: has_discount || false,
        discount_percent: has_discount ? Number.parseInt(discount_percent) : 0,
      })
      .select()
      .single()

    if (packError) {
      console.error("[v0] Error inserting pack:", packError)
      return NextResponse.json(
        { 
          error: "Failed to create pack",
          details: `Database error: ${packError.message}`,
          code: packError.code
        },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      pack,
      message: "Pack uploaded successfully"
    })
  } catch (error: any) {
    console.error("[v0] Upload error:", error)
    return NextResponse.json(
      { 
        error: "Internal server error",
        details: error.message || "An unexpected error occurred during upload",
        message: error.toString()
      },
      { status: 500 }
    )
  }
}
